/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #000000;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(233, 210, 0, 0.795);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.yolk {
  color: yellow;
  font-size: 1rem;
  font-family: "Poppins";
}

.forge-interface {
  /* border: 1px solid red; */
  padding: 10px;
  overflow: hidden;
}
.title {
  /* border: 1px solid green; */
  text-align: center;
  font-size: 30px;
  font-weight: 500;
  color: rgb(233, 210, 0);
  text-shadow: 5px 5px 20px #000000;
}
.content {
  /* border: 1px solid yellow; */
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.forge-container {
  margin-top: 50px;
  /* border: 4px solid black; */
  background-color: #222222d3;
  padding: 20px;
  gap: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 70vw;
  height: 600px;
  overflow-y: scroll;
}
.forge-item {
  border: 1px solid rgb(114, 114, 114);
  padding: 20px;

  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.forge-item h1 {
  text-align: center;
  font-size: 30px;
  font-weight: 500;
  color: rgb(233, 210, 0);
  text-shadow: 1px 1px 5px #000000;
}
.forge-item-img {
  height: 100%;
  width: 70%;
}

.forge-btn {
  outline: 1px solid rgb(114, 114, 114);
  padding: 10px 15px;
  width: 200px;
  align-self: center;
  border-radius: 30px;
  background-color: #222222;
  color: #fff;
  font: "Poppins", sans-serif;
  transition: 0.3s ease;
  letter-spacing: 3px;
  border: none;
  margin-bottom: 30px;
}
.forge-btn:hover {
  cursor: pointer;
  background-color: #757575;
  letter-spacing: 4px;
}
.img {
  transform: translateY(-10px);
  transition: 1s ease;
}
.img:hover {
  transform: translateY(0px);
  scale: 1;
}
@media screen and (max-width: 625px) {
  .title {
    font-size: 20px;
  }
}
@media screen and (max-width: 400px) {
  .title {
    font-size: 15px;
    margin-bottom: 20px;
  }
}
